import react, { useRef, useEffect } from "react";
import "./Contact.scss";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import { mapRange } from "gsap/gsap-core";
import cv from "../cv.pdf";

gsap.registerPlugin(ScrollTrigger);

function Contact() {
	const date = new Date();
	const currentYear = date.getFullYear();
	let contactLinks = useRef(null);
	let footer = useRef(null);

	var tlText = gsap.timeline();
	var tl = gsap.timeline();

	function ContactText(text) {
		let arrayText = new Array(text.length);

		for (let i = 0; i < text.length; ++i) {
			arrayText[i] = text.charAt(i);
		}

		return arrayText;
	}

	function LetterSpan(props) {
		let color = null;

		if (props.color == "#466ec7") {
			color = { color: "#466ec7" };
		}

		return (
			<span style={color} className="letter" id={props.index}>
				{props.letter}
			</span>
		);
	}

	let firstText = ContactText("Let's create something together,");
	let secondText = ContactText("Drop me a ");
	let thirdText = ContactText("line!");

	useEffect(() => {
		console.log(contactLinks);

		let p = document.querySelectorAll(".div p");

		for (let i = 0; i < p.length; ++i) {
			let obj = ".border.border" + i;

			let hover = gsap.to(obj, {
				width: "100%",
				duration: 0.5,
				paused: true,
				ease: "power1.inOut",
			});

			p[i].addEventListener("mouseenter", () => hover.play());
			p[i].addEventListener("mouseleave", () => hover.reverse());
		}

		tlText.to(".letter", 0.9, {
			top: -55,
			stagger: 0.04,
		});
		tl.from(contactLinks.current, 1.2, {
			x: "-100%",
			opacity: 0,
		})
			.from(
				".contact-links .div",
				1.2,
				{
					rotation: 4,
					scale: 0.7,
					y: 15,
				},
				"-=1"
			)
			.from(
				".contact-links p",
				1.5,
				{
					opacity: 0,
					stagger: 0.2,
				},
				"-=1"
			)
			.from(
				footer.current,
				2,
				{
					opacity: 0,
					// y: 30,
				},
				"-=1.3"
			);

		ScrollTrigger.create({
			animation: tlText,
			trigger: "#contact",
			toggleActions: "play none none reverse",
			start: "20% 90%",
			end: "91% buttom",
			// markers: true,
		});

		ScrollTrigger.create({
			animation: tl,
			trigger: ".contact-links",
			toggleActions: "play reverse reverse reverse",
			// start: "top"
			start: "20% 90%",
			// end: "91% top",
			// markers: true,
		});
	}, []);

	return (
		<section id="contact">
			<div className="contact-container">
				<div className="contact-text">
					<h3>
						<span className="hidden-text1">
							{firstText.map((value, index) => {
								return <LetterSpan index={index} letter={value} />;
							})}
						</span>
					</h3>
					<h3 className="lighter-h3">
						<span className="hidden-text2">
							{secondText.map((value, index) => {
								return <LetterSpan index={index} letter={value} />;
							})}
							{thirdText.map((value, index) => {
								return (
									/* <LetterSpan index={index} letter={value} /> */
									<LetterSpan color="#466ec7" index={index} letter={value} />
								);
							})}
						</span>
					</h3>
				</div>
				<div ref={contactLinks} className="contact-links">
					<div className="div">
						<div>
							<p>
								<a href="mailto:popescu.traian2003@gmail.com">
									popescu.traian2003@gmail.com
								</a>
							</p>
							<div className="border border0"></div>
						</div>
						<div>
							<a>
								<p>
									<a
										href="https://www.linkedin.com/in/traian-popescu-9681b5212/"
										target="_blank"
									>
										Linkedin
									</a>
								</p>
							</a>
							<div className="border border1"></div>
						</div>
						<div>
							<a>
								<p>
									<a href="https://github.com/goblinupufos" target="_blank">
										Github
									</a>
								</p>
							</a>
							<div className="border border2"></div>
						</div>
						<div>
							<a>
								<p>
									<a href={cv} target="_blank">
										CV
									</a>
								</p>
							</a>
							<div className="border border3"></div>
						</div>
					</div>
				</div>
				<p ref={footer} className="footer">
					&copy; {currentYear} Traian Popescu
				</p>
			</div>
		</section>
	);
}

export default Contact;
