import react from "react";
import Project from "./Project";
import projects from "../ProjectsData";
import "./Work.scss";

function Work() {
	function ProjectCard(index, src, title, description) {
		return (
			<div className="project" id="index">
				<Project
					id={index}
					index={index}
					src={src}
					title={title}
					description={description}
				/>
			</div>
		);
	}

	return (
		<section id="work">
			{projects.map((value, index) =>
				ProjectCard(index, value.src, value.title, value.description)
			)}
		</section>
	);
}

export default Work;
