import react, { useEffect, useRef } from "react";
import "./CustomCursor.scss";

function CustomCursor() {
	const cursorRef = useRef(null);
	const secCursorRef = useRef(null);

	useEffect(() => {
		document.addEventListener("mousemove", (event) => {
			const { clientX, clientY } = event;
			const mouseX = clientX - cursorRef.current.clientWidth;
			const mouseY = clientY - cursorRef.current.clientHeight;

			cursorRef.current.style.transform = `translate3d(${mouseX}px, ${mouseY}px, 0)`;
			cursorRef.current.style.transform = `translate3d(${mouseX}px, ${mouseY}px, 0)`;
		});

		document.addEventListener("mousemove", (event) => {
			const { clientX, clientY } = event;
			const mouseX = clientX - secCursorRef.current.clientWidth;
			const mouseY = clientY - secCursorRef.current.clientHeight;

			secCursorRef.current.style.transform = `translate3d(${mouseX}px, ${mouseY}px, 0)`;
			secCursorRef.current.style.transform = `translate3d(${mouseX}px, ${mouseY}px, 0)`;
		});
	}, []);

	return (
		<div className="cursor">
			<div ref={cursorRef} className="main-cursor"></div>
			<div ref={secCursorRef} className="sec-cursor"></div>
		</div>
	);
}

export default CustomCursor;
