import react, { useEffect, useRef } from "react";
import "./Project.scss";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import { Power3 } from "gsap/gsap-core";

gsap.registerPlugin(ScrollTrigger);

function Project(props) {
	let imageContainer = useRef(null);
	let image = useRef(null);
	let cover = useRef(null);
	let textContainer = useRef(null);
	let title = useRef(null);
	let description = useRef(null);
	let triggerImage = ".image" + props.index;
	let triggerText = ".text" + props.index;
	var tlImage = gsap.timeline();
	var tlText = gsap.timeline();

	useEffect(() => {
		tlImage
			.to(imageContainer.current, 0.6, {
				css: {
					visibility: "visible",
				},
			})
			.to(image.current, 0, {
				css: {
					visibility: "visible",
				},
			})
			.to(cover.current, 1.6, {
				height: 0,
				ease: Power3.easeInOut,
			})
			.from(
				image.current,
				1.6,
				{
					scale: 1.4,
					ease: Power3.easeInOut,
					// delay: -1,
				},
				"-=1.6"
			);

		tlText
			.to(textContainer.current, 1, {
				css: {
					visibility: "visible",
				},
			})
			.from(title.current, 1.6, {
				y: -60,
				opacity: 0,
				// delay: ,
			})
			.from(
				description.current,
				1.6,
				{
					y: -120,
					opacity: 0,
				},
				"-=1.6"
			);

		ScrollTrigger.create({
			animation: tlImage,
			trigger: triggerImage,
			toggleActions: "play reverse play reverse",
		});

		ScrollTrigger.create({
			animation: tlText,
			trigger: triggerImage,
			toggleActions: "play reverse play reverse",
		});
	}, []);

	let reverseFlex = {
		flexDirection: "row-reverse",
	};

	let reverseMargin = { marginRight: "100px" };

	if (props.index % 2 !== 1) {
		reverseFlex = null;
		reverseMargin = {
			marginLeft: "100px",
		};
	}

	return (
		<div className="container" style={reverseFlex}>
			<div ref={imageContainer} className={"image image" + props.index}>
				<div ref={cover} className="cover"></div>
				<img ref={image} src={props.src} />
			</div>
			<div
				ref={textContainer}
				className={"text text" + props.index}
				style={reverseMargin}
			>
				<div>
					<h3 ref={title} className="title">
						{props.title}
					</h3>
					<p ref={description} className="description">
						{props.description}
					</p>
				</div>
			</div>
		</div>
	);
}

export default Project;
